import { graphql, Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import Layout from '../components/layout'
import SEO from '../components/seo'

import site from '../../config/site'

import style from '../styles/archive.module.css'

const _ = require('lodash-addons')

const metaImage = site.image

// Sort object alphabetically function
const propComparator = (propName) => (a, b) =>
  a[propName].toLowerCase() == b[propName].toLowerCase()
    ? 0
    : a[propName].toLowerCase() < b[propName].toLowerCase()
    ? -1
    : 1

const AuthorsPage = ({
  data: {
    allAuthorYaml: {
        nodes: authors
    },
    allMarkdownRemark: {
        group
    }
  }
}) => {
  // Convert object array into hashmap
  const lut = authors.reduce((map, author) => (map[author.id] = author.name, map), {})
  console.log(lut)
  return (
    <Layout>
      <SEO
        title={`All Authors | ${site.titleAlt}`}
        path="/author/"
        description="An archive of posts organized by author."
        metaImage={metaImage}
      />
      <main id="main" className={style.main}>
        <div className={style.title}>
          <h1 className={style.heading}>
            <span>All authors</span>
          </h1>
        </div>
        <div className={style.content}>
          <h2 className={style.subHeading}>
            <span>Browse by author</span>
          </h2>
          <div className={style.columnList}>
            <ul>
              {group.sort(propComparator(`fieldValue`)).map((author) => {
                const matched = lut[author.fieldValue]
                const authorName = matched ? matched : author.fieldValue
                return (
                  <li key={author.fieldValue}>
                    <Link to={`/author/${_.slugify(author.fieldValue)}/`}>
                      <strong>{authorName}</strong>{' '}
                      <span className={style.count}>{author.totalCount}</span>
                    </Link>
                  </li>
                )})
              }
            </ul>
          </div>
        </div>
      </main>
    </Layout>
  )
}

AuthorsPage.propTypes = {
  data: PropTypes.object.isRequired,
}

export const pageQuery = graphql`
  query AuthorsQuery {
    allAuthorYaml {
      nodes {
        id
        name
      }
    }
    allMarkdownRemark(filter: { frontmatter: { published: { ne: false } } }) {
      group(field: frontmatter___authors___id) {
        fieldValue
        totalCount
      }
    }
  }
`

export default AuthorsPage
